import {InjectionToken} from '@angular/core';

const apiUrl = 'https://indigo-conf.herokuapp.com/api';
export const API_URL = new InjectionToken('API_URL');


// API endpoints
export const ApiUrl = {
   get users() {
    return `${apiUrl}/users`;
  },

  user(id: number) {
    return `${apiUrl}/users/${id}`;
  }
};
