import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {MatDialog, MatSnackBar} from '@angular/material';

import {User} from '../../app.model';
import {DataService} from '../../shared/data.service';
import {DeleteUserComponent} from '../../shared/delete-user.component';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {
  profileForm: FormGroup;
  private userId: string;
  userWasChanged = false;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private dataService: DataService,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.route.data.subscribe((data: {content: User}) => {
      const user = data.content;
      this.userId = user.id;

      this.profileForm = new FormGroup({
        name: new FormControl(
          user.name,
          Validators.required
        ),
        email: new FormControl(
          user.email,
          [Validators.required, Validators.email]
        )
      });
    });
  }

  deleteUser() {
    const dialogRef = this.dialog.open(DeleteUserComponent, {
      data: {
        message: 'Dou you really want to delete this user?'
      }
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        this.dataService.deleteUser(this.userId).subscribe((data) => {
          this.snackBar.open(
            'User was successfully deleted',
            'Close',
            {duration: 3000}
          );

          this.router.navigate(['/users']);
        });
      }
    });
  }

  // saving user
  onProfileFormSubmit() {
    this.dataService.saveUser(this.userId, {
      id: this.userId,
      name: this.profileForm.value.name,
      email: this.profileForm.value.email
    }).subscribe(() => {
      this.userWasChanged = false;

      this.snackBar.open(
        'User is successfully saved',
        'Close',
        {duration: 3000}
      );
    });
  }

  // check form input values changes to make 'save' button active
  onValueChange() {
    if (!this.userWasChanged) {
      this.userWasChanged = true;
    }
  }

  backToUsers() {
    // if user data was changed we ask for confirm before leaving the page
    if (this.userWasChanged) {
      const dialogRef = this.dialog.open(DeleteUserComponent, {
        data: {
          message: 'User not saved. Are you sure you want to leave the page?'
        }
      });

      dialogRef.afterClosed().subscribe((result) => {
        if (result) {
          this.router.navigate(['/users']);
        }
      });

    } else {
      this.router.navigate(['/users']);
    }
  }
}
