import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {MatDialog, MatSnackBar} from '@angular/material';
import {Router} from '@angular/router';
import { v4 as uuidv4 } from 'uuid';

import {DataService} from '../../shared/data.service';
import {DeleteUserComponent} from '../../shared/delete-user.component';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {
  addForm: FormGroup;
  constructor(private router: Router,
              private dataService: DataService,
              private snackBar: MatSnackBar,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.addForm = new FormGroup({
      id: new FormControl(
        uuidv4(), // user can change generated id
        Validators.required
      ),
      name: new FormControl(
        null,
        Validators.required
      ),
      email: new FormControl(
        null,
        [Validators.required, Validators.email]
      )
    });
  }

  // creating user
  onAddFormSubmit() {
    this.dataService.createUser({
      id: this.addForm.value.id,
      name: this.addForm.value.name,
      email: this.addForm.value.email
    }).subscribe((data) => {
      this.snackBar.open(
        'User created',
        'Close',
        {duration: 3000}
      );

      this.router.navigate([`/users/${this.addForm.value.id}`]);
    });
  }

  // cancel user creating
  cancelUser() {
    const dialogRef = this.dialog.open(DeleteUserComponent, {
      data: {
        message: 'User not saved. Are you sure about leaving this page?'
      }
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        this.router.navigate(['/users']);
      }
    });
  }
}
