import {Component, OnInit, ViewChild} from '@angular/core';
import {MatPaginator, MatSnackBar, PageEvent} from '@angular/material';
import {Router} from '@angular/router';

import {User} from '../../app.model';
import {DataService} from '../../shared/data.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;

  users: User[];
  pageEvent: PageEvent;
  totalCount: number;
  limit = 10;

  constructor(private router: Router,
              private snackBar: MatSnackBar,
              private dataService: DataService) { }

  ngOnInit() {
    this.getUsers();
  }

  onCardClick(id: string) {
    if (id === '') {
      this.snackBar.open(
        'User can not be modified. Contact admin.',
        'Close',
        {duration: 5000}
      );
    } else {
      this.router.navigate([`/users/${id}`]);
    }
  }

  getUsers(event?: PageEvent): void {
    const limit = event ? event.pageSize : this.limit;
    const offset = event ? (event.pageIndex) * limit : 0;

    this.dataService.getUsers(offset, limit).subscribe(
      (data: [User[], number]) => {
        this.users = data[0];
        this.totalCount = data[1];
      }
    );
  }
}
