import {Inject, Injectable} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {Observable, zip} from 'rxjs';
import {map} from 'rxjs/operators';

import {API_URL} from '../app.config';
import {User} from '../app.model';

@Injectable()
export class DataService {

  constructor(private http: HttpClient,
              @Inject(API_URL) private ApiUrl) { }

  getUsers(offset: number, limit: number): Observable<[User[], number]> {
    const params = new HttpParams()
      .set(`offset`, offset.toString())
      .set(`limit`, limit.toString());

    // second request made in order to receive total number of users. Should be changed in future
    return zip(
      this.http.get<User[]>(this.ApiUrl.users, {params}),
      this.http.get<User[]>(this.ApiUrl.users).pipe(
        map(data => data.length)
      )
    );
  }

  getUserById(id: string): Observable<User> {
    return this.http.get<User>(this.ApiUrl.user(id));
  }

  saveUser(id: string, user: User) {
    return this.http.put<User>(this.ApiUrl.user(id), user);
  }

  deleteUser(id: string) {
    return this.http.delete<User>(this.ApiUrl.user(id));
  }

  createUser(user: User) {
    return this.http.post<User>(this.ApiUrl.users, user);
  }
}
